*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto-regular', 'sans-serrif';
}

@font-face {
  font-family: 'Roboto-Light';
  font-style: normal;
  src: url(./fonts/roboto/Roboto-Light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: 'Roboto-regular';
  font-style: normal;
  src: url(./fonts/roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

:root {
  --white-color : rgb(235, 235, 235);
}

header {
  width: 100%;
  min-height: 100vh;
  background-color: rgb(84, 156, 156);
  background-image: url(./img/1.jpg);
  background-size: cover;
  background-attachment: fixed;
}
header::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 100vh;
  opacity: 0.8;
  display: block;
  background: rgb(255,162,0);
  background: linear-gradient(146deg, rgb(8, 32, 78) 0%, rgb(133, 84, 0) 100%);
}
header::after {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(0deg, #000000c9 0%, #ffffff00 18%);
}
.site-nav {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 70px;
  background-color: rgba(0, 0, 0, 0.473);
  display: flex;
  align-items: center;
}
.site-nav-list {
  width: 60%;
  margin: 0 auto;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.site-nav-list li:hover {
  transition: .3s;
  background-color: var(--white-color);
}
.site-nav-list li a {
  padding: 10px;
  text-decoration: none;
  color: var(--white-color);
}
.site-nav-list li a:hover {
  color: #222222;
}
.header-content {
  position: relative;
  z-index: 2;
}
.header-content .text-box {
  padding-top: 10%;
  color: var(--white-color);
}
h1 {
  font-family: 'Roboto-regular';
  transition: .6s;
  font-size: 100px;
  width: max-content;
  margin: 0 auto;
}
h1::after {
  content: '';
  width: 100%;
  margin: 0 auto;
  display: block;
  height: 2px;
  background: var(--white-color);
  transition: .6s;
}
.text-hidden h1 {
  opacity: 0;
  transform: translateY(-10%);
}
.text-hidden h1::after {
  width: 0%;
}

.header-content .text-box p {
  font-family: 'Roboto-light';
  font-size: 30px;
  text-align: center;
  transition: .9s;
}

.text-hidden .info-text {
  opacity: 0;
  transform: translateY(20%);
}

/* MAIN -------------------------------------------------- */


section {
  min-height: 100vh;
  background: #15182e;
  padding-top: 30px;
}

.section-title {
  font-family: 'Roboto-regular';
  font-size: 70px;
  color: var(--white-color);
  text-align: center;
}
.gallery {
  padding: 40px;
}
.gallery .content-box {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 0 5%;
  position: relative;
}
.gallery .content-box svg {
  width: 40%;
}
.gallery-box {
  padding: 20px;
  width: 59%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.gallery-item {
  width: 48%;
  height: 200px;
  display: inline-block;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}
.preview-image {
  width: 200%;
  position: absolute;
}
.gallery-item .info-box {
  padding: 10px 20px 20px;
  background-color: rgba(0, 0, 0, 0.342);
  left: 0;
  right: 0;
  position: absolute;
  bottom: 0;
}
.message-text {
  margin-top: 20px;
  font-size: 40px;
  color: var(--white-color);
  position: relative;
  z-index: 10;
}
.photos-gallery {
  width: 100%;
  min-height: 670px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}
.photos-gallery .photo-wrapper {
  width: 32%;
  height: 200px;
  overflow: hidden;
  display: inline-block;
  margin-top: 10px;
  background-color: #140a2c;
  position: relative;
}
.info-box {
  position: relative;
}
.info-box .album-title {
  color: var(--white-color);
  font-size: 20px;
}
.info-box .photosCount {
  margin-top: 5px;
  color: var(--white-color);
  font-size: 25px;
}
.info-box .open-album-btn {
  position: absolute;
  right: 10px;
  top: 10px;
  background-color: #5068d3;
  color: var(--white-color);
  font-family: 'Roboto-Regular', 'sans-serrif';
  font-size: 17px;
  padding: 3px;
  border: none;
  outline: transparent;
  border-radius: 5px;
}
.info-box .open-album-btn:hover {
  background-color: #7188ee;
}
.info-box .open-album-btn:active {
  transform: scale(1.1);
}
.info-box .photosCount::before {
  content: '';
  width: 20px;
  height: 20px;
  display: inline-block;
  background-size: cover;
  background-image: url(./img/photo-file.svg);
} 
.photos-gallery .photo {
  height: inherit;
  transform: translateX(-50%);
  position: absolute;
  left: 50%;
  transition: .3s;
  opacity: 0;
}
.photos-gallery .photo-wrapper:hover .photo{
  transform: scale(1.1) translateX(-50%);
}
.gallery-menu {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  padding: 20px;
  background-color: #101018c7;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  width: 14%;
}
.gallery-menu button {
  width: 30px;
  height: 30px;
  background-image: url(./img/Arrow.svg);
  background-size: contain;
  outline: transparent;
  border: transparent;
  background-color: transparent;
  transition: .3s;
}
.gallery-menu .back {
  transform: scale(-1, 1);
}
.gallery-menu button:hover {
  background-color: #15182e;
}
.gallery-menu button:active {
  background-color: #2e3780;
}
.gallery .close-btn {
  top: 0;
  left: 20px;
  position: absolute;
  width: 50px;
  height: 50px;
  z-index: 1;
  background-color: #272727ea;
  border: none;
  outline: transparent;
  border-radius: 5px;
  background-image: url(./img/Arrow.svg);
  background-size: cover;
  transform: scale(-1, 1);
  transition: .3s;
}
.gallery .close-btn:hover {
  background-color: #a3a3a3c0;
}
.gallery .close-btn:active {
  background-color: #a3a3a3c0;
}

.photo-view {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #15182ea9;
  padding: 25px;
}
.photo-view img {
  height: 100%;
}
.photo-box {
  height: 100%;
  width: max-content;
  margin: 0 auto;
  position: relative;
}
.close-photo {
  width: 50px;
  height: 50px;
  background-color: #c93838;
  outline: none;
  border: solid #b31b1b 2px;
  border-radius: 50%;
  position: absolute;
  top: -20px;
  right: -20px;
  background-image: url(./img/close.svg);
  background-size: cover;
}
.quiz-section {
  background-image: url(./img/1827px-Ilja_Jefimowitsch_Repin_-_Reply_of_the_Zaporozhian_Cossacks_-_Yorck.jpg);
  background-attachment: fixed;
  background-size: cover;
  position: relative;
}
.quiz-section::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0.8;
  display: block;
  background: rgb(255,162,0);
  background: linear-gradient(146deg, rgb(8, 32, 78) 0%, rgb(133, 84, 0) 100%);
}
.section-title {
  position: relative;
  z-index: 4;
}
.quiz-box {
  position: relative;
  z-index: 4;
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  width: 60%;
  height: 400px;
  background-color: #121627d5;
  border-radius: 20px;
}
.question-text {
  min-height: 150px;
  font-family: 'Roboto-Regular', 'sans-serrif';
  font-size: 40px;
  color: var(--white-color);
  text-align: center;
}
.quiz-box {
  transition: .3s;
}
.answers-field {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.answers-field img {
  width: 100%;
  display: block;
  margin: 0 auto;
}
.answers-field .quiz-photo-wrapper {
  width: 40%;
  height: min-content;
}
.answers-field form {
  width: 55%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
.answers-field form label:first-of-type {
  margin-top: 0;
}
.answers-field form label {
  width: 100%;
  margin-top: 30px;
  font-size: 20px;
  padding: 5px;
  background: linear-gradient(180deg, rgba(255,162,0,1) 0%, rgba(199,137,29,1) 100%);
  border-radius: 10px;
  color: var(--white-color);
  text-shadow: 1px 1px 2px black;
}
.answers-field form label:hover {
  background: linear-gradient(180deg, rgb(252, 175, 44) 0%, rgb(255, 193, 86) 100%);
}
.answers-field form label:active {
  background: linear-gradient(180deg, rgb(252, 183, 65) 0%, rgb(223, 173, 87) 100%);
}
.answers-field form label input {
  display: none;
}

.success-bar {
  width: 90%;
  height: 40px;
  background-color: #9b9b9b;
}
.success-scale {
  height: inherit;
  background-color: #6ece5b;
}
.finished {
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  width: 30%;
  height: 400px;
  position: relative;
  z-index: 4;
  background: #15182ea8;
  border-radius: 20px;
}
.finished {
  color: var(--white-color);
}
.finished h2 {
  text-align: center;
}
.finished .text {
  margin-top: 30px;
  font-size: 20px;
}
.finished .result-text {
  font-size: 30px;
}
.finished .success-bar {
  margin-top: 20px;
}
.finished .try-again {
  margin: 0 auto;
  display: block;
  padding: 10px 5px;
  font-size: 20px;
  border-radius: 10px;
  color: var(--white-color);
  background-color: #5068d3;
  margin-top: 100px;
  border: transparent;
  outline: transparent;
}
.finished .try-again:hover {
  background-color: #7188ee;
}
.video-container {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 90%;
  position: relative;
  z-index: 2;
}
.video-container iframe{
  margin: 0 auto;
  margin-top: 30px;
}
.video-section {
  padding: 20px;
  overflow: hidden;
  position: relative;
}
.video-section::before {
  content: '';
  width: 10%;
  height: 200vh;
  display: block;
  position: absolute;
  z-index: 1;
  background-image: url(./img/film-strip.svg);
  transform: rotate(30deg) translateY(-30%);
  animation: strip-anim-1 alternate 3s ease-in-out infinite;
}
.video-section::after {
  content: '';
  width: 10%;
  height: 200vh;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  background-image: url(./img/film-strip.svg);
  transform: rotate(-45deg) translateY(-100%);
  animation: strip-anim-2 alternate 3s ease-in-out infinite;
}


@keyframes strip-anim-1 {
  0% {
    transform: rotate(30deg) translateY(-20%);
  }
  100% {
    transform: rotate(30deg) translateY(-30%);
  }
}
@keyframes strip-anim-2 {
  0% {
    transform: rotate(-20deg) translateY(-40%);
  }
  100% {
    transform: rotate(-20deg) translateY(-30%);
  }
}

.history-section {
  position: relative;
  background-size: cover;
  background-image: url(./img/rubo-franc-kazaki-u-gornoy.jpg);
  background-attachment: fixed;
  padding: 20px;
}
.history-section::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0.8;
  display: block;
  background: rgb(255,162,0);
  background: linear-gradient(146deg, rgb(8, 32, 78) 0%, rgb(133, 84, 0) 100%);
}
.history-card {
  width: 40%;
  min-height: 400px;
  margin-top: 10px;
  background-color: rgba(212, 212, 212, 0.555);
  position: relative;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75), inset 0px 0px 70px 0px rgba(0, 0, 0, 0.473);
  border-radius: 20px;
  padding: 20px;
}
.history-card:first-of-type {
  margin-top: 30px;
}
.history-card:nth-of-type(2n) {
  margin-left: 59%;
}
.history-card .photo-wrapper{
  width: 90%;
  height: 300px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
}
.history-card .photo-wrapper img {
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.history-card .history-text {
  margin: 30px;
  font-family: 'Roboto-Regular';
  font-size: 20px;
}
footer {
  height: 150px;
  background-color: #15182e;
  position: relative;
}
footer p {
  color: #979797;
  position: absolute;
  bottom: 10px;
  left: 10px;
}
footer::before {
  content: '';
  display: block;
  width: 200px;
  height: inherit;
  background-image: url(./img/e13e11278a9c6cf7bde187e48da47565.png);
  background-size: cover;
  position: absolute;
  top: 0;
  right: 0;
}

@media (max-width: 1200px) {
  header {
    background-attachment: local !important;
  }
  section {
    background-attachment: local !important;
  }
  .header-content {
    display: flex;
    flex-direction: column-reverse;
    height: 100vh;
  }
  header .text-box {
    margin-bottom: 30%;
  }
  .site-nav {
    height: 40%;
    align-self: flex-end;
  }
  .site-nav-list {
    width: 100%;
    flex-direction: column;
  }
  .site-nav-list li {
    display: inline-block;
    width: max-content;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
    font-size: 20px;
  }
  .site-nav-list li::after {
    content: '';
    width: 100%;
    height: 2px;
    display: block;
    margin: 0 auto;
    margin-top: 5px;
    background-color: var(--white-color);
  }
  h1 {
    font-size: 50px;
  }
  .emblem-of-Ukraine {
    display: none;
  }
  .section-title {
    font-size: 40px;
  }
  .gallery {
    padding-bottom: 50px;
  }
  .gallery-box {
    width: 90%;
    margin: 0 auto;
  }
  .gallery-item {
    width: 100%;
    height: 300px;
    margin-top: 20px;
  }
  .gallery-item img {
    transform: scale(1.5);
    width: 200%;
  }
  .photos-gallery {
    margin-top: 10px;
    max-height: 80vh;
    overflow: auto;
  }
  .photos-gallery .photo-wrapper {
    width: 49%;
  }
  .photo-box {
    width: 100%;
    height: auto;
  }
  .gallery .close-btn {
    width: 100px;
    height: 100px;
  }
  .close-photo {
    top: 0;
    right: 0;
    width: 100px;
    height: 100px;
  }
  .photo-box img {
    width: 100%;
    height: auto;
  }
  .gallery-menu {
    width: 60%;
  }
  .gallery-menu button {
    width: 100px;
    height: 100px;
  }
  .gallery-menu button:hover {
    background-color: transparent;
  }
  .gallery-menu button:active {
    background-color: #2e38807c;
  }
  .quiz-box {
    width: 95%;
  }
  .finished {
    width: 90%;
  }
  .answers-field {
    flex-direction: column;
  }
  .answers-field form label:first-of-type {
    margin-top: 20px;
  }
  .answers-field .quiz-photo-wrapper {
    width: 100%;
    margin-top: 20px;
  }
  .quiz-box {
    height: unset;
    min-height: 400px;
  }
  .quiz-section {
    padding: 20px 0;
  }
  .answers-field form {
    width: 90%;
  }
  .answers-field form label {
    padding: 5px 15px;
    font-size: 23px;
  }
  .history-card {
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
  }
  .history-card:nth-of-type(2n) {
    margin: 0 auto;
    margin-top: 20px; 
  }
  @media (max-width: 800px) {
    h1 {
      font-size: 30px;
    }
    .info-box .open-album-btn {
      position: unset;
      margin-top: 20px;
    }
    .photos-gallery .photo-wrapper {
      width: 100%;
    }
    .photos-gallery.photo {
      width: 100%;
      height: auto;
    }
    .gallery .close-btn {
      width: 70px;
      height: 70px;
      left: 5px;
    }
    .gallery {
      padding: 40px 0;
    }
    .question-text {
      font-size: 20px;
    }
  }
}